<template>
	<v-app>
		<v-content id="mainContent">
			<full-page id="fullpage" ref="fullpage" :options="options">
				<v-container
					id="firstPage"
					class="section"
					fluid
					data-anchor="vitrine"
				>
					<v-alert v-if="notChromeOrFirefox" type="warning">
						Dveloppement effecut sous Firefox, vrifi sur Chrome. Prfrez utiliser un de ces deux navigateurs.
					</v-alert>
					<h1>
						Visualisation des habitudes de consommation des smartphones
					</h1>
					<div style="text-align: center; margin-top: 7%; font-size: 1.3em">
						Anthony Bardou, Hugo Polloli, Tristan Syrzisko et Tho Rabut
					</div>
					<div
						style="text-align: center; position: absolute; bottom: 7%; width: 100%"
					>
						<v-btn
							color="#f5f9f9"
							style="color: #252835;"
							@click="$refs.fullpage.api.moveSectionDown()"
						>
							Next
						</v-btn>
					</div>
				</v-container>
				<v-container
					class="section"
					fluid
					data-anchor="explications"
				>
					<v-row>
						<v-col cols="6" style="text-align: center; margin: auto; padding: 0 3%; margin-bottom: 3%;">
							<h2 style="text-transform: uppercase">
								Contexte
							</h2>
							<p>Le smartphone est une technologie qui s'est ancre profondment dans nos socits dveloppes. Outil permettant de naviguer parmi un grand ensemble d'applications, nous l'utilisons plusieurs fois par jour, chacun  notre manire. Dans ce projet, nous avons voulu reprsenter les profils, les faons dont les diffrents utilisateurs naviguent dans leur tlphone. Cette visualisation, qui s'inscrit dans le contexte de l'explosion des flux de donnes et du <em>Quantified Self</em>, permet  un utilisateur curieux comme  un expert cherchant  visualiser des comportements d'explorer diffrents profils de consommation selon diffrents critres comme la priode temporelle, la localisation ou encore le temps d'utilisation de chaque application.</p>
						</v-col>
						<v-col cols="6" style="text-align: center; padding: 0 3%">
							<h2 style="text-transform: uppercase">
								Collection des donnes
							</h2>
							<p>Les donnes que nous utilisons ont t collectes par nos soins, sur nous-mmes, grce  l'application App Usage. Elle permet un export de l'activit de l'utilisateur du tlphone sur lequel elle est installe. En croisant ces donnes avec nos historiques des positions (collects et fournis par Google), nous pouvons alors constituer un dataset en JSON liant temps d'utilisation des applications, positions gographiques et temporelles.</p>
						</v-col>
					</v-row>
					<v-row>
						<v-col cols="6" style="text-align: center; padding: 0 3%">
							<h2 style="text-transform: uppercase">
								Graphe de switch
							</h2>
							<p>Dans cette premire visualisation, les donnes sont reprsentes par un graphe, dans lequel les noeuds incarnent les applications (dont la taille varie en fonction de leur temps d'utilisation). Deux noeuds sont lis si l'utilisateur est pass d'une application  l'autre (la largeur des artes reprsentant le nombre de fois que l'utilisateur a effectu ce basculement). En jouant avec les filtres lis  la visualisation, on peut se rendre compte de plusieurs types de consommation diffrentes, en fonction du lieu et de l'heure de la journe.</p>
						</v-col>
						<v-col cols="6" style="text-align: center; padding: 0 3%">
							<h2 style="text-transform: uppercase">
								Arbre radial
							</h2>
							<p>Pour cette seconde visualisation, les donnes sont reprsentes par un arbre radial (Radial Tree) et reprsente des enchainements d'applications, du moment o le tlphone est dverrouill jusqu'au moment o il se remet en veille. Chaque noeud du graphe est une application, et chaque arte entre deux noeuds reprsente un switch entre ces deux applications. Cette visualisation nous permet alors d'explorer les squences les plus frquentes d'applications, et nous donne la possibilit de visualiser le profil de consommation d'un utilisateur d'une manire diffrente.</p>
						</v-col>
