					<v-row v-if="fetched">
						<v-col cols="5" style="border-right: 1px solid black;">
							<!-- <v-card style="background-color: #2B2B3B"> -->
							<FilterGroup v-if="visu" />
							<FilterGroupRadial v-else />
							<!-- </v-card> -->
						</v-col>
						<v-col cols="7">
							<!-- <v-card> -->
							<ForceGraph v-if="visu" @changeVisu="changeVisu" />
							<RadialTree v-else @changeVisu="changeVisu" />
							<!-- </v-card> -->
						</v-col>
					</v-row>
					<v-container v-else style="text-align: center;">
						<v-progress-circular
							style="margin-top: 200px;"
							:size="200"
							:width="20"
							color="red"
							indeterminate
						/>
					</v-container>				
				</v-container>
			</full-page>
		</v-content>
	</v-app>
</template>

<script>
// Filters
import FilterGroupRadial from '@/components/FilterGroupRadial.vue';
import FilterGroup from '@/components/FilterGroup.vue';
import ForceGraph from '@/components/visus/ForceGraph.vue';
import RadialTree from '@/components/visus/RadialTree.vue';


export default {
	name: 'Home',
	components: {
		FilterGroup,
		FilterGroupRadial,
		ForceGraph,
		RadialTree
	},
	data: () => ({
		fetched: false,
		// True: forceGraph False : dendogram
		visu: true,
		notChromeOrFirefox: false,
		options: {
			licenseKey: 'vhs@RA^t1',
			anchors: ['vitrine', 'explications', 'visualisation'],
			sectionsColor: ['#252835', '#252835', '#252835'],
			navigation: true,
			navigationPosition: 'right',
			navigationTooltips: ['Premire Page', 'Explications', 'Visualisation']
			// loopTop: true,
			// loopBottom: true
		}
	}),
	mounted() {
		document.documentElement.addEventListener('mouseup', e => {
			this.$store.commit('setDraggingState', {
				dragging: false,
				dragStartRing: null,
				firstObject: null
			});
		});

		const uA = navigator.userAgent;

		if (uA.indexOf('Firefox') == -1 && uA.indexOf('Chrome') == -1) {
			this.notChromeOrFirefox = true;
		}

		fetch('dataset.json').then(async resp => {
			let data = await resp.json();

			this.$store.commit('setBrutDataset', data);

			if (this.visu) {
				data = data.filter(d => d['App Name'] != 'Screen off' && !d['App Name'].startsWith('Screen on'));
				this.$store.commit('setDataset', data);
				this.$store.commit('setFilteredDataset', data);
				this.$store.commit('setFinalDataset', data);
				// const str = JSON.stringify(data);
			} else {
				this.$store.commit('setDataset', data);
				this.$store.commit('setFilteredDataset', data);
				this.$store.commit('setFinalDataset', data);

			}

			this.fetched = true;
		});
	},
	methods: {
		changeVisu(val) {
			this.visu = val;

			let data = this.$store.state.brutData;

			if (val) {
				data = data.filter(d => d['App Name'] != 'Screen off' && !d['App Name'].startsWith('Screen on'));
				this.$store.commit('setDataset', data);
				this.$store.commit('setFilteredDataset', data);
				this.$store.commit('setFinalDataset', data);
			} else {
				this.$store.commit('setDataset', data);
				this.$store.commit('setFilteredDataset', data);
				this.$store.commit('setFinalDataset', data);
			}
		}
	}
};
</script>

